import React, { FC } from 'react'
import { Link } from 'react-router-dom'

type PropsType = {
    link: { path: string; title: string }
    icon?: any
    style?: Object
}
/**
 *   LinkTo,       /  ().
 * @function
 * @param {Object} props -  .
 * @param {{path: string, title: string}} props.link - ,   path  title  .
 * @returns {JSX.Element} React-,    .
 */
export const LinkTo: FC<PropsType> = ({ link, icon, style }) => {
    return (
        <Link
            to={link.path}
            style={style && style}
        >
            {icon}
            {link.title}
        </Link>
    )
}
